import React from 'react';
import type { WhiteMarginAnalysis, DeviceOrientationAnalysis } from '../features/mockup/utils/debugAnalysis';

interface DebugPanelProps {
  whiteMarginAnalyses: WhiteMarginAnalysis[];
  orientationAnalyses: DeviceOrientationAnalysis[];
  isVisible: boolean;
}

export const DebugPanel: React.FC<DebugPanelProps> = ({
  whiteMarginAnalyses,
  orientationAnalyses,
  isVisible
}) => {
  if (!isVisible) return null;

  return (
    <div className="fixed bottom-0 left-0 right-0 bg-black bg-opacity-90 text-white p-4 max-h-96 overflow-y-auto z-50">
      <h3 className="text-lg font-bold mb-4"> </h3>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        {/*  */}
        <div>
          <h4 className="text-md font-semibold mb-2 text-yellow-400"> </h4>
          {whiteMarginAnalyses.map((analysis, index) => (
            <div key={index} className="mb-3 p-2 border border-gray-600 rounded">
              <div className="text-sm">
                <div className="font-semibold text-cyan-400"> {analysis.deviceIndex + 1}</div>

                {analysis.hasWhiteMargin ? (
                  <div className="text-red-400">
                     
                    <div className="ml-4 mt-1">
                      <div>: {analysis.marginLocations.top}px</div>
                      <div>: {analysis.marginLocations.bottom}px</div>
                      <div>: {analysis.marginLocations.left}px</div>
                      <div>: {analysis.marginLocations.right}px</div>
                      <div className="font-bold text-yellow-400 mt-1">
                        : {analysis.requiredBleedPercentage}%
                      </div>
                    </div>
                  </div>
                ) : (
                  <div className="text-green-400"> </div>
                )}

                <div className="text-xs text-gray-400 mt-1">
                  : {analysis.detectedWhitePixels}/{analysis.totalEdgePixels}
                  ({(analysis.whitePixelRatio * 100).toFixed(2)}%)
                </div>

                {analysis.recommendations.length > 0 && (
                  <div className="mt-2 text-xs">
                    <div className="font-semibold">:</div>
                    {analysis.recommendations.map((rec, i) => (
                      <div key={i} className="ml-2"> {rec}</div>
                    ))}
                  </div>
                )}
              </div>
            </div>
          ))}
        </div>

        {/*  */}
        <div>
          <h4 className="text-md font-semibold mb-2 text-green-400"> </h4>
          {orientationAnalyses.map((analysis, index) => (
            <div key={index} className="mb-3 p-2 border border-gray-600 rounded">
              <div className="text-sm">
                <div className="font-semibold text-cyan-400">
                   {analysis.deviceIndex + 1} ({analysis.deviceType})
                </div>

                <div className="ml-4 mt-1">
                  <div>: {Math.round(analysis.deviceRotation)}</div>
                  <div>: {Math.round(analysis.majorAxisAngle)}</div>

                  {analysis.notchPosition.angle > 0 && (
                    <div className="text-yellow-400">
                      : ({Math.round(analysis.notchPosition.x)}, {Math.round(analysis.notchPosition.y)})
                      <br />
                      : {Math.round(analysis.notchPosition.angle)}
                    </div>
                  )}

                  <div className="font-bold text-green-400 mt-1">
                    : {Math.round(analysis.recommendedImageRotation)}
                  </div>
                </div>

                <div className="text-xs text-gray-400 mt-1">
                  : {analysis.analysisDetails.aspectRatio.toFixed(2)}
                  {analysis.analysisDetails.isPortrait && ' ()'}
                  {analysis.analysisDetails.isLandscape && ' ()'}
                  {analysis.analysisDetails.isDiagonal && ' ()'}
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>

      {/*  */}
      <div className="mt-4 pt-4 border-t border-gray-600">
        <h4 className="text-md font-semibold mb-2"> </h4>
        <div className="text-sm">
          {whiteMarginAnalyses.filter(a => a.hasWhiteMargin).length > 0 ? (
            <div className="text-red-400">
               {whiteMarginAnalyses.filter(a => a.hasWhiteMargin).length}
            </div>
          ) : (
            <div className="text-green-400">
               
            </div>
          )}

          <div className="mt-1">
            : {
              orientationAnalyses.filter(a => a.recommendedImageRotation !== 0).length
            }
          </div>
        </div>
      </div>
    </div>
  );
};